import React from 'react'
import './List.css'
function List(props) {
    // <div>
  return (
        <div  className="center-div">
            {/* <div className="center-divide"> */}
                <button onClick={()=>{props.onSelect(props.id)}}>Delete</button>
                <div className="notes">
                    {props.text}
                </div>
            {/* </div> */}
            {/* </div> */}
        </div>
  )
}

export default List